import React, { useState, useEffect } from "react"
import { useUser } from '../context/UserContext'
import Layout from "../components/layout"
import Seo from "../components/seo"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import styled from "styled-components"
import  PremiumContent  from "../components/PremiumContent"
// import LeftIcon from "../images/left-icon.svg"
// import RightIcon from "../images/right-icon.svg"

const StyledDiv = styled.div`
  & h1 {
    font-size: 2rem;
    font-weight: 700;
    line-height: 1.5;
  }
  & h2 {
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 2.2;
  }
  & h3 {
    font-size: 1.2rem;
    font-weight: 600;
    line-height: 2;
  }
  & h4 {
    font-size: 1.1rem;
    font-weight: 600;
    line-height: 2;
  }
  & a {
    color: #6b46c1;
  }
`
const BlogPost = props => {
  // OPTIONAL PREVIOUS/NEXT BUTTONS
  // const nextSlug = pageContext.next ? pageContext.next.fields.slug : "/"
  // const previousSlug = pageContext.previous
  //   ? pageContext.previous.fields.slug
  //   : "/"
  // const nextLinkStatus = pageContext.next
  //   ? pageContext.next.frontmatter.templateKey === "blog-post"
  //     ? true
  //     : false
  //   : false
  // const previousLinkStatus = pageContext.previous
  //   ? pageContext.previous.frontmatter.templateKey === "blog-post"
  //     ? true
  //     : false
  //   : false

  const { pageContext } = props
  const post = props.data.ghostPost
  let date = new Date(post.published_at) // assuming post.frontmatter.date is in ISO string format
  let options = { year: "numeric", month: "short", day: "numeric" }
  let formattedDate = date.toLocaleDateString("en-US", options)
  let isoDate = date.toISOString().split("T")[0] // get the date part of the ISO string

  const { user, isPaidUser, loading, isLoggedIn } = useUser;
  const [showFullContent, setShowFullContent] = useState(false);

  useEffect(() => {
    const canViewFull = isPaidUser || post.visibility === 'public' || 
                       !post.tiers?.some(tier => tier.type === 'paid');
    setShowFullContent(canViewFull);
  }, [isPaidUser, post, loading]);

  if (loading) {
    return <div className="loading">Loading...</div>;
  }

  function getContent (){
    // Public Post - everyone can see
    if(post.visibility === "public" || !post.tiers?.some(tier => tier.type === "paid")) {
      return (
        post.html
      )
    }
  
    // Not logged in
    if (!isLoggedIn) {
      return (
        post.excerpt
      )
    }

  }

  return (
    <Layout>
      <Seo
        title={post.title}
        description={post.excerpt}
      />
      <main className="pt-8 pb-16 lg:pt-16 lg:pb-24">
        <div className="flex justify-between px-4 mx-auto max-w-screen-xl min-w-1/2">
          <article className="mx-auto w-full max-w-4xl text-xl format format-sm sm:format-base lg:format-lg format-blue dark:format-invert">
            <header className="mb-4 lg:mb-6 not-format">
              <h1 className="mb-4 text-3xl font-extrabold leading-tight text-[#000000] lg:mb-6 lg:text-4xl dark:">
                {post.title}
              </h1>
            </header>
            {post.feature_image && (
              <div className="post-content-image">
                <GatsbyImage
                  image={getImage(post.featuredImg)}
                  className="lg:mb-2 overflow-hidden rounded-xl"
                  alt={post.title}
                />
              </div>
            )}
            <p className="text-base text-gray-500 dark:text-gray-400 lg:mb-2">
              <time dateTime={isoDate} title="Date">
                {formattedDate}
              </time>
            </p>
            <PremiumContent post={post} />
          </article>
        </div>
      </main>
    </Layout>
  )
}

export default BlogPost

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
      }
    }
    ghostPost(slug: { eq: $slug }) {
    ...GhostPostFields
    }
  }
`
